ones.")]),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(8),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Inversión"}},[_c('p',[_vm._v("En términos de inversión, los tratados de libre comercio han proporcionado un marco de seguridad jurídica y estabilidad para los inversionistas extranjeros. Esto ha sido clave para fomentar la llegada de capital extranjero en sectores estratégicos como infraestructura, manufactura, tecnologías de la información, servicios financieros y energías renovables. Por ejemplo, tras la entrada en vigencia del TLC con Estados Unidos en 2012, Colombia experimentó un aumento en la inversión de empresas estadounidenses, principalmente en el sector minero-energético y servicios empresariales.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Inversión extranjera directa"}},[_c('p',[_vm._v("De acuerdo con cifras del Banco de la República, la inversión extranjera directa ha fluctuado, pero sigue siendo un componente importante del crecimiento económico. En 2022, la IED alcanzó los USD 17.048 millones, y cerca del 70 % se concentró en el sector de petróleo, minería y energía. Sin embargo, también se ha registrado un aumento progresivo en áreas como servicios y agroindustria, lo cual refleja el potencial de los TLC para diversificar las fuentes de capital externo.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Exportaciones"}},[_c('p',[_vm._v("En cuanto a las exportaciones, los tratados han facilitado el acceso preferencial a nuevos mercados, reduciendo o eliminando aranceles que anteriormente limitaban la competitividad de los productos colombianos. En el caso del TLC con la Unión Europea, se han abierto oportunidades para el banano, las flores y el café colombiano, productos que actualmente gozan de reconocimiento en estos mercados gracias a su calidad y sostenibilidad.")])])])],1)]),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(9),_c('