sociales")]),_c('p',[_vm._v("Otra consecuencia importante se relaciona con los efectos ambientales y sociales. los acuerdos de libre comercio han impulsado actividades extractivas que ponen en riesgo la biodiversidad, como la expansión de monocultivos, la minería a gran escala y la presión sobre territorios indígenas y campesinos. En muchos casos, la apertura comercial ha favorecido la inversión en estos sectores sin una adecuada regulación ambiental, generando conflictos sociales y deterioro ecológico (Gómez Lee, 2009).")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema8/21.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg-0"},[_c('h5',[_vm._v("Impacto desigual entre regiones del país")]),_c('p',[_vm._v("Además, el libre comercio ha tenido un impacto desigual entre regiones del país. Las zonas con mayor infraestructura, acceso a mercados y apoyo institucional han logrado aprovechar mejor las oportunidades del comercio exterior, mientras que regiones más apartadas, como algunas zonas del Pacífico y la Amazonía, han quedado rezagadas, profundizando las brechas regionales. Esto ha generado desequilibrios en el desarrollo económico y social del país.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema8/22.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg-0"},[_c('h5',[_vm._v("Cláusulas de propiedad intelectual estrictas")]),_c('p',[_vm._v("También es necesario mencionar que algunos tratados, al incluir cláusulas de propiedad intelectual estrictas, han limitado el acceso a tecnologías, medicamentos genéricos y semillas para la agricultura local. Esto ha generado tensiones entre la soberanía alimentaria y los intereses de grandes corporaciones, afectando a comunidades rurales y a productores tradici